import logo from './logo.svg';
import './App.css';
import React from 'react';



class App extends React.Component {
  constructor(props) {
    super(props);
    this.symbols = ['GOOG','FB','BLKB','JKHY','TXN', 'APPL'];
    this.state = { _data: {}, newTicker: '' };
  }

  componentDidMount = () => {
    fetch('/api?symbols='  + this.symbols.join(',')).then(response => {
      if(response.ok) {
        return response.json();
      } else {
        return {'Error': 'error while fetching data'}
      }
    }).then(data => {
      if(data.Error) {
        alert("error while fetching data");
      } else {
        this.setState({_data: data});
      }      
    });
  }

  addToTicker = () => {
    if(this.state.newTicker) {
      fetch('/api?symbols='  + this.state.newTicker).then(response => {
        if(response.ok) {
          return response.json();
        } else {
          alert("error while fetching data");
          this.setState({newTicker: ''});
          return {'Error': 'Error'};
        }
      }).then(data => {
        if(data.Error) {
          console.log('Error while fetching data');
        } else {
          this.symbols.push(this.state.newTicker.toUpperCase());
          this.setState({newTicker: ''});
          this.componentDidMount();
        }        
      });
    }
  }

  newTicker = (event) => {
    this.setState({
      newTicker: event.target.value
    });
  }

  render() {
      return (
        <div className="App">
          <header className="App-header">
            <div className="marquee">
              <span>
                {
                  this.symbols.filter(Symbol => this.state['_data'] && this.state['_data'][Symbol] && this.state['_data'][Symbol].price.regularMarketPrice).map(Symbol => {
                    return ( <span>{(Symbol + ':' + 
                              ( (this.state['_data'] && this.state['_data'][Symbol] &&this.state['_data'][Symbol].price.currencySymbol) || ' ') + 
                              ((this.state['_data'] && this.state['_data'][Symbol] && this.state['_data'][Symbol].price.regularMarketPrice) || 'Fetching Data '))} </span>);
                  })
                }
              </span>
            </div>
            <div className="inputField">
              <input type="text" id="inputField" value={this.state.newTicker} onChange={(event) => this.newTicker(event)}/>
              <button onClick={this.addToTicker}>Add to Ticker</button>
            </div>
          </header>
        </div>
      );
    }
}

export default App;
